/* MOBILE */
.app__list-item {
  margin: 0 0 2.7em 0;
  padding: 0;
  color: #F2C0B6; }

.app__list-link {
  text-decoration: none; }

/* TABLET */
@media all and (min-width: 768px) {
  .app__list {
    /* margin: 0 10%; */
    display: grid;
    grid-gap: 2em;
    grid-template-columns: 19em 19em; }
  .app__list-item {
    width: 19em;
    margin-bottom: 0;
    background-color: #665855; } }

/* DESKTOP */
@media all and (min-width: 1200px) {
  .app__filter-full-name {
    height: 100%;
    width: 50em; }
  .app__list {
    grid-template-columns: 19em 19em 19em; } }

.character__detail {
  padding-top: 10px;
  margin: 0 auto;
  width: 14.07em;
  color: #F2C0B6; }

.character__detail-info {
  display: flex;
  flex-direction: column;
  align-items: center; }

.character__detail-image {
  height: 300px;
  display: block; }

.character__detail-house, .character__detail-birth, .character__detail-patronus, .character__detail-status {
  align-self: flex-start;
  margin: 0.5em 0 0.5em 2em; }

.status, .house {
  display: flex;
  align-items: base; }

.house__img, .status__alive, .status__death {
  height: 50px;
  width: 50px; }

.house__name, .status__name {
  margin: 1em 1em 0 0; }

/* TABLET */
@media all and (min-width: 768px) {
  .character__detail {
    padding-left: 1em;
    width: 28em;
    background-color: #665855; }
  .character__detail-wrapper {
    display: flex; }
  .character__detail-info {
    padding-left: 1em; } }

/* DESKTOP */
@media all and (min-width: 1200px) {
  .character__detail {
    width: 54em; }
  .character__detail-image {
    height: 500px;
    width: 350px;
    display: block; }
  .character__detail-name {
    font-size: 3em;
    margin: 0.5em 0 0.5em 1.3em; }
  .character__detail-house, .character__detail-birth, .character__detail-patronus, .character__detail-status {
    font-size: 2em; }
  .house__img, .status__alive, .status__death {
    height: 70px;
    width: 70px; }
  .house__name, .status__name {
    margin: 0.5em 0.5em 0 0; } }

.character {
  text-align: center; }

.character__image {
  height: 25em;
  width: 19em;
  /* background-repeat: no-repeat;
    background-position: center;
    background-size: contain; */
  object-fit: cover;
  object-position: 5px 10%; }

/* MOBILE */
.app__filter-itm {
  text-align: center;
  margin-top: 0.5em;
  margin-bottom: 0.5em;
  height: 2em; }

.app__filter-full-name {
  height: 100%;
  width: 19em;
  border: 2px solid brown;
  border-radius: 4px;
  color: white;
  background-color: lightblue;
  font-size: 1em; }

.app__filter-full-name:focus {
  border: 3px solid #555; }

.app__list {
  margin: 0;
  padding: 2em 0;
  list-style: none; }

/* MOBILE */
.app__go-back-link {
  text-decoration: none; }

.app__go-back-button {
  width: 9em;
  height: 3em;
  border-radius: 5px;
  border: 2px solid black;
  background-color: #F2C0B6;
  margin: 1em auto; }

.app__go-back-text {
  margin: 0;
  text-align: center;
  line-height: 3em; }

/* DESKTOP */
@media all and (min-width: 1200px) {
  .app__go-back-button {
    width: 18em;
    height: 6em; }
  .app__go-back-text {
    line-height: 3em;
    font-size: 2em; } }

/* MOBILE */
.app__header {
  text-align: center; }

.app__title {
  text-align: center;
  font-size: 2em;
  margin-top: 0.5em;
  margin-bottom: 0.5em; }

/*TABLET*/
@media all and (min-width: 768px) {
  .app__main {
    display: flex;
    justify-content: space-around; } }

@media all and (min-width: 1200px) {
  .app__title {
    font-size: 5em; } }
